/* color checker */

$dark: #141414;
$sblack: #0A0A0A;
$light: #B3B3B3;
$primary: #00C700;

/* fonts */

@font-face {
	font-family: "alien";
	src: url("../assets/fonts/alienleagueii.ttf"); }

@font-face {
	font-family: "helv";
	src: url("../assets/fonts/Helvetica.otf"); }

@font-face {
	font-family: "bil";
	src: url("../assets/fonts/MISTRAL.ttf"); }

$alF: "alien", Helvetica;
$hel: "helv";
$b: "bil";

/* reset body and option block */

body {
	margin: 0;
	padding: 0;
	color: $light;
	background-color: $sblack;
	background-image: url("../assets/img/BGprincipal.png");
	background-attachment: fixed;
	overflow-x: hidden; }

.flex {
	display: flex;
	width: 100%; }
/* menu */
.menu {
	height: 50px;
	border-bottom: 5px solid $dark;
	background-image: url("../assets/img/BGbar.png");
	position: fixed;
	top: 0;
	z-index: 2; }
a, a:hover, a:visited {
	text-decoration: none;
	color: $light; }
.menu-item {
	margin: auto;
	padding-top: 10px;
	padding-left: 10px;
	padding-right: 10px;
	font-family: $alF;
	font-size: 1.5em;
	height: 42px; }


.border {
	border-bottom: 1px solid transparent; }
.border:hover {
	text-shadow: 1px 1px 5px $primary;
	border-bottom: 1px solid $primary; }

/* svg path */

.ico {
	height: 50px;
	margin-top: -15px; }

.highlight {
	fill: $light; }

.highlight:hover {
	fill: $primary;
	filter: drop-shadow(1px 1px 3px $light); }

/* header */

#hidden_top {
	height: 30vh; }
@keyframes bganimate {
	from {
		background-position: 50% 50% , 50% 100%; }
	to {
		background-position: 50% 50% , 50% 60%; } }

.top-section {
	animation-name: bganimate;
	animation-duration: 10s;
	transition: height 0.5s;
	animation-direction: alternate-reverse;
	animation-iteration-count: infinite;
	height: 50vh;
	background-color: $dark;
	background-image: url("../assets/img/BGfront.svg"),url("../assets/img/BGback.png");
	background-attachment: fixed scroll fixed;
	background-blend-mode: difference;
	background-position: 50% 50% , 50% 60%;
	background-size: inherit, cover, cover;
	background-repeat: no-repeat , no-repeat;
	border-bottom: 5px solid $dark;
	position: fixed;
	top: 50px; }

.container-name {
	margin: auto;
	text-align: center; }

@keyframes logoanim {
	0% {
		transform: rotateY(-20deg); }
	50% {
		transform: rotateY(20deg); }
	100% {
		transform: rotateY(0deg); } }

.logo {
	height: 32vh;
	animation-name: logoanim;
	animation-duration: 5s;
	transition: height 0.5s;
	animation-direction: alternate-reverse;
	animation-timing-function: linear;
	animation-iteration-count: infinite;
	background-image: url("../assets/img/BG.png");
	background-size: 307% 242%;
	background-position: 48% 44%;
	perspective: 150px;
	z-index: 9999; }
#double_ligne {
	color: $light;
	font-family: $alF;
	font-size: 0.8em;
	height: 12px;
	width: 100vw;
	padding-top: 2px;
	padding-bottom: 2px;
	border-bottom: 1px dashed $light;
	border-top: 1px dashed $light;
	background-image: url("../assets/img/BGbar.png");
	margin-top: 50.5vh;
	transition: margin-top 0.5s;
	position: fixed;
	z-index: 9999; }


marquee {
	margin-top: -2px; }
/* description */

section.grid {
	display: grid;
	grid-template-columns: 2fr 2fr 4fr 2fr;
	grid-gap: 100px;
	margin-top: 60vh;
	@media screen and (max-width : 768px) {
		display: block;
		padding: 25px; }
	@media screen and (min-width : 769px) and (max-width: 1420px) {
		grid-template-columns: 1fr 20fr 40fr 1fr;
		grid-gap: 10px; } }


@keyframes avatar {
	from {
		transform: rotateX(90deg)rotateY(90deg); }
	to {
		transform: rotateX(0deg)rotateY(0deg); } }

.avatar {
	grid-column: 2/3;
	@media screen and (max-width : 768px) {
		float: left;
		width: 35%;
		margin: 25px;
		z-index: 1; } }
.avatar img {
	width: 100%;
	animation-name: avatar;
	animation-duration: 3.5s;
	z-index: 1; }
.desc {
	grid-column: 3/4;
	border-left: 1px solid $primary;
	padding-left: 15px;
	font-family: $hel; }

.desc::first-line {
	color: $light; }
#mica {
	float: right;
	color: $light;
	font-family: $b;
	opacity: 1;
	font-size: 2em;
	font-weight: 100; }
.bigL {
	font-size: 2em;
	color: $primary; }

section.section1 {
	background-image: url("../assets/img/BGtriangle.svg");
	background-repeat: no-repeat no-repeat;
	background-size: cover;
	background-blend-mode: soft-light;
	margin-top: 1vh; }

section.comp√©tences {
	width: 50vw; }

@keyframes scaleright {
	from {
		transform: translate(-200vw,0px); }
	to {
		transform: translate(0vw,0px); } }

h2.titlesection {

	text-align: center;
	font-family: $alF;
	font-size: 3em;
	font-weight: 100;
	margin: 12px;
	margin-left: 15vw;
	margin-top: 5vh;
	padding-right: 60vw;
	border-bottom: 1px solid $primary; }
.scaleright {
	animation-name: scaleright;
	animation-duration: 1.6s; }

@keyframes scaleleft {
	from {
		transform: translate(200vw,0px); }
	to {
		transform: translate(0vw,0px); } }

.content_logo {
	width: 60vw;
	margin: auto;
	padding: 12px; }
.anim {
	animation-name: scaleleft;
	animation-duration: 1.6s; }

@keyframes logoturn {
	from {
		transform: rotateY(90deg); }
	to {
		transform: rotateY(0deg); } }

object.logoS {
	// animation-name: logoturn
	// animation-duration: 6s
	max-width: 100%;
	min-width: 8%; }

object.logoSA {
	animation-name: logoturn;
	animation-duration: 6s;
	max-width: 100%;
	min-width: 8%; }
.logoS {
	height: 50px;
	fill: $light;
	margin: auto; }



@keyframes rotateBar {
	from {
		transform: rotateX(-90deg); }
	to {
		transform: rotateX(0deg); } }
hr {
	border: 1px solid $light; }

hr.animateHR {
	animation-name: rotateBar;
	animation-duration: 8s; }

p.description_1 {
	margin: auto;
	font-size: 1.2em;
	width: 70vw;
	text-indent: 5em;
	padding: 1em;
	font-family: $hel; }

p.description_1::first-letter {
	font-size: 2em;
	line-height: 0.4;
	color: $light; }



.content-paragraphe {
	width: 70vw;
	margin: auto; }


p.parcours {
	margin: auto;
	width: 10vw; }

section.section2 {
	background-image: url("../assets/img/BGtriangle2.svg");
	background-repeat: no-repeat no-repeat;
	background-size: cover;
	background-blend-mode: soft-light; }

.xp {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
	grid-gap: 25px;
	font-size: 20px;
	font-family: $hel;
	margin-top: 50px;
	padding-bottom: 50px;
	@media screen and (max-width: 697px) {
		grid-template-columns: 1fr 1fr; } }
.xp1 {
	@media screen and (min-width: 698px) {
		grid-column: 2/3; } }

.xp2 {
	@media screen and (min-width: 698px) {
		grid-column: 3/4; } }

.xp3 {
	@media screen and (min-width: 698px) {
		grid-column: 4/5; } }

.xp4 {
	@media screen and (min-width: 698px) {
		grid-column: 5/6; } }

.xp5 {

	@media screen and (min-width: 698px) {
		grid-column: 6/7; } }



.date {
	color: $primary;
	font-size: 25px;
	text-align: center;
	font-family: $alF; }

ul.galery_content {
	display: grid;
	padding-left: 10vw;
	padding-right: 10vw;
	grid-gap: 10px;
	grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
	grid-auto-rows: 250px;
	grid-auto-flow: dense;
	perspective: none;
	list-style: none; }

.wide {
	grid-column: span 2; }

.tall {
	grid-row: span 2; }
.box {
	grid-row: span 2;
	grid-column: span 2; }


@keyframes imgAnim {
	from {
		width: 0%;
		height: 0%; }

	to {
		width: 100%;
		height: 100%; } }

.img_content img {
	animation-name: imgAnim;
	animation-duration: 5s;
	display: block;
	width: 100%;
	height: 100%;
	object-fit: cover; }


